<template>
  <div  align='center'>
    <b-form @submit="handleLogin">
      <b-container fluid >

                <b-row  class="my-1">
                  <b-col sm="2"><label for="userid">ID:</label></b-col>
                  <b-col sm="6">
                    <b-form-input
                      id="userid"
                      v-model="user.userid"
                      type="text"
                      required
                      placeholder="ID.."
                      size="sm"
                    ></b-form-input>
                  </b-col>
                </b-row>
                <b-row  class="my-1">
                  <b-col sm="2"><label for="pass">PASS:</label></b-col>
                  <b-col sm="6">
                    <b-form-input
                      id="pass"
                      v-model="user.pass"
                      type="password"
                      required
                      placeholder="Pass.."
                      size="sm"
                    ></b-form-input>
                  </b-col>
               </b-row>
            
              <b-row  class="my-1">      
                 <b-col sm="8" > <b-button type="submit" variant="outline-primary" size="sm">Submit</b-button></b-col>
              </b-row>
      </b-container>

    </b-form>
    
  </div>
</template>
<script>


import User from '../models/user';

export default {
    name: 'Login',
    data() {
        return {
            user: new User('','','', '', ''),
            loading: false,
            message: ''
        }
    },
    computed: {
        loggedIn() {
            return this.$store.state.initialState.status.loggedIn;
        }
    },
    created() {
        if (this.loggedIn) {
            this.$router.push('/');
        }
    },
    methods: {
        handleLogin(evt) {

         // alert('111111');
           var idleng = 0;
           var passleng = 0;

           idleng = (this.user.userid).length;
           passleng = (this.user.pass).length;


            evt.preventDefault();
            console.log(this.user.userid);
            //alert('체크 1' + (this.user.userid).length +':::' + (this.user.pass).length);
            //alert('체크 1:::' + (this.user.userid) + aaa);
            this.loading = true;
            if (idleng > 0  && passleng > 0) {
                    //    if ( (this.user.username).length > 0  && (this.user.password).length > 0 ) {
                    //  if ((this.user.username).length > 0) {
                    //alert('sdfsddsf');
                    
                    // 로그인 시작 
                    this.$store.dispatch('ulogin', this.user).then(
                        () => {
                           // alert('성공');
                            this.$router.push('/');
                        },
                        error => {
                            alert('실패');
                            this.loading = false;
                            this.message = 
                            (error.response && error.response.data) || error.message || error.toString();
                            alert( error.message  );
                        }
                    );
             }else{
                alert('아이디와 패스워드를 입력 바랍니다 !!');

             }
        },

        registerUser() {
            this.$router.push({ name: 'Register'});
        }

	}
}

</script>

