<template>
  <div>
    <b-row >      
    <b-col sm="8"  class="pb-2"> <Header/></b-col>
    </b-row>

   <b-row >      
    <b-col sm="8"  class="pb-2"> <TodoList/></b-col>
    </b-row>


    <b-row >      
    <b-col sm="8"  class="pb-2"> <b-button variant="danger" @click="logout">logOut</b-button></b-col>
    </b-row>

  </div>
</template>


<script>
import User from '../models/user';
import Header from "../components/Header";
import TodoList from "../components/todo/TodoList";


export default {
    /*
    name: 'Login',
    data() {
        return {
            user: new User('', '', '','',''),
            loading: false,
            message: ''
        }
    },
    */
    name: 'Login,App',
    data() {
        return {
            user: new User('', '', '','',''),
            loading: false,
            message: ''
        }
    },
    components: {
    Header,TodoList
    },
    computed: {
        loggedIn() {
           // alert('Home');
            return this.$store.state.initialState.status.loggedIn;
        }
    },
    created() {
        if (this.loggedIn) {
            //alert('로그인됐다면 ???');
            this.$router.push('/');
            //this.$router.push({ name: 'Login', query: { to: to.path }});
        }
    },
    methods: {
      logout() {
             this.$store.dispatch('logout', this.user).then(
                        () => {
                            //alert('성공');
                            this.$router.push('/');
                            location.reload();
                        },
                        error => {
                            alert('실패');
                            this.loading = false;
                            this.message = 
                            (error.response && error.response.data) || error.message || error.toString();
                            alert( error.message  );
                        }
                    );
        }

	}

    
}


</script>