<template>

  <div>
     <table class="table table-striped">
            <thead>
                <tr>
                    <th>등록일</th>
                    <th><b-input v-model="regdate" id="regdate" placeholder="날짜를 입력해주세요."></b-input></th>
                </tr>


                <tr>
                    <th>등록일</th>
                    <th><b-input v-model="title" id="title" placeholder="제목을 입력해주세요."></b-input></th>
                </tr>


                <tr>
                    <th>등록일</th>
                    <th>
                      <b-form-textarea
                        v-model="contents"
                        id="contents"
                        placeholder="내용을 입력해 주세요"
                        rows="3"
                        max-rows="6"
                    ></b-form-textarea>
                    </th>
                </tr>
    
                 
            </thead>           
      </table>
    <b-button @click="updateMode ? updateContent() : uploadContent()">저장</b-button>&nbsp;
    <b-button @click="cancle">취소</b-button>
  </div>


</template>

<script>
import axios from 'axios';
import data from '@/data';




export default {
  name: 'ToDoCreate',
