      updateObject: {},
      updateMode: this.$route.params.contentId > 0 ? true : false,
    }
  },
  created() {
      if(this.$route.params.contentId > 0) {


        const contentId = Number(this.$route.params.contentId)
        //alert('contentId:'+ contentId);
         //this.updateObject = data.Content.filter(contentItem => contentItem.content_id === contentId)[0]
         this.listData(contentId);
         // obj =  Object.assign({}, this.listData(contentId))
         // alert('regdate:'+ updateObject.REGDATE);
      } 
  },
  methods: {

    listData(id) {
      // alert("reg_date:" + id);
       axios.post(this.$SEND_API_URL + 'todo/todDo.do', {
            REGDATE: id
            //PASS: user.pass
          }, {
            headers: {'Content-Type': 'application/json'}
          })
          .then(response => {
               //alert('응답값  PASS::::::'+ JSON.stringify(response.data.PASS));
               //alert('응답값  LOGIN_CHK ::::::'+ JSON.stringify(response.data.LOGIN_CHK));
              //alert('서버호출됨 !! ');
              //alert('응답값  REGDATE::::::'+ JSON.stringify(response.data.REGDATE));
              //localStorage.setItem('todo', JSON.stringify(response.data));

              //alert('response.data: ' + JSON.stringify(response.data));

             // var object ;
              //  object = response.data;

             // this.updateObject = response.data;

             // alert('object: ' + object.REGDATE);
              this.regdate = response.data.REGDATE;
              this.title = response.data.TITLE;
              this.contents = response.data.CONTENTS;

              //return object;
              //this.updateObject = response.data;
          });
            /*
            this.$axios.get('/api/boards')
            .then(response => {
                this.list = response.data.data;
            })
            .catch(error => {
                console.log(error)
            })
            */

           //  this.list = TodoService.todDoList();
            
            
           // alert("this.list:::::" + (this.list)[0].TITLE);
           },

    uploadContent() { // 저장
  
           var regdateleng = 0;
           var titleleng = 0;
           var contentsleng =0;

           regdateleng = (this.regdate).length;
           titleleng = (this.title).length;
           contentsleng = (this.contents).length;
          //alert('contents:' + this.contents );



           if (regdateleng > 0  && titleleng > 0 ) {
              alert('등록 시작 !!');

             axios.post(this.$SEND_API_URL + '/todo/insertTodo.do', {
