              })
             
            });



           }else{
             alert('날짜,제목,내용을  입력 바랍니다 !!');
             return;
           }


    },
    updateContent() { // 수정
      /*regdate
        this.updateObject.title = this.subject;
        this.updateObject.context = this.context;
        this.$router.push({
          path: '/'
        })
      */
var regdateleng = 0;
           var titleleng = 0;
