               //alert('응답값  PASS::::::'+ JSON.stringify(response.data.PASS));
               alert('응답값 RESULT :  '+ JSON.stringify(response.data.RESULT));


               this.$router.push({
                path: '/'
              })
             
            });



           }else{
             alert('날짜,제목,내용을  입력 바랍니다 !!');
             return;
           }
 



    },
    cancle() {
      this.$router.push({
        path: '/'
      })
    }
  }
}
</script>

