<template>
  <div >
    <b-form @submit="handleLogin">
      <b-row>
        <b-col sm="3"><label for="username">Username:</label></b-col>
        <b-col sm="9">
          <b-form-input
            id="username"
            v-model="user.username"
            type="text"
            required
            placeholder=""
          ></b-form-input>
        </b-col>
      </b-row>
      <b-row>
        <b-col sm="3"><label for="age">age:</label></b-col>
        <b-col sm="9">
          <b-form-input
            id="age"
            v-model="user.age"
            type="text"
            required
            placeholder=""
          ></b-form-input>
        </b-col>
      </b-row>
      <b-button type="submit" variant="primary">Submit</b-button>
      <b-button variant="danger" @click="registerUser">Register</b-button>
    </b-form>
    <b-card class="mt-3" header="Form Data Result">
      <pre class="m-0"></pre>
    </b-card>
  </div>
</template>
<script>
import User from '../models/user';


export default {
    name: 'Login',
    data() {
        return {
            user: new User('', '', '','',''),
            loading: false,
            message: ''
        }
    },
    computed: {
        loggedIn() {
            return this.$store.state.initialState.status.loggedIn;
        }
    },
    created() {
        if (this.loggedIn) {
            this.$router.push('/');
        }
    },
    methods: {
        handleLogin(evt) {
            evt.preventDefault();
            //console.log(this.user.username);
             //    alert('체크 1' + (this.user.username).length +':::' + (this.user.password).length);
            this.loading = true;
            if (this.user.username && this.user.age) {
                    //    if ( (this.user.username).length > 0  && (this.user.password).length > 0 ) {
                    //  if ((this.user.username).length > 0) {
                    //alert('sdfsddsf');
                    
                    // 로그인 시작 

                    
                    this.$store.dispatch('login', this.user).then(
                        () => {
                            alert('성공');
                            this.$router.push('/');
                        },
                        error => {
                            alert('실패');
                            this.loading = false;
                            this.message = 
                            (error.response && error.response.data) || error.message || error.toString();
                            alert( error.message  );
                        }
                    );
             }
        },

        registerUser() {
            this.$router.push({ name: 'Register'});
        }

	}
}

</script>

