<template>
  <div>
     <table class="table table-striped">
            <thead>
                <tr>
                    <th>등록일</th>
                    <th>제목</th>
                    <th>내용</th>
                </tr>
                 <tr v-for="item in list" :key="item.REGDATE" @click="showInfo(item.REGDATE)" style="cursor:pointer;">
                            <td align='center'>{{item.REGDATE}}</td>
                            <td align='center'>{{item.TITLE}}</td>
                            <td align='center'>{{item.CONTENTS}}</td>
                 </tr>
               
                 
            </thead>           
      </table>
    <b-button @click="writeContent">글쓰기</b-button>
  </div>
</template>

<script>
import axios from 'axios';
//import TodoService from '../../services/todo.service';

//const API_URL = 'http://localhost:8090/todo/';



export default {
  name: "TodoList",
  created() {
        this.listData()
  },
  data() {
    return {
            selected: false,
            list: [],
            item: null
        }
  },
  methods: {
    listData() {
       //alert("URL:" + this.$SEND_API_URL);
       axios.post(this.$SEND_API_URL + 'todo/todDoList.do', {
            //LOGID: user.userid,
            //PASS: user.pass
          }, {
            headers: {'Content-Type': 'application/json'}
          })
          .then(response => {
            //alert('응답값  PASS::::::'+ JSON.stringify(response.data.PASS));
            //alert('응답값  LOGIN_CHK ::::::'+ JSON.stringify(response.data.LOGIN_CHK));
    
              //alert('서버호출됨 !! ');
              //alert('응답값  REGDATE::::::'+ JSON.stringify(response.data[0].REGDATE));
              //localStorage.setItem('todo', JSON.stringify(response.data));
              //return response.data;
              this.list = response.data;
          });
            /*
            this.$axios.get('/api/boards')
            .then(response => {
                this.list = response.data.data;
            })
            .catch(error => {
                console.log(error)
            })
            */

           //  this.list = TodoService.todDoList();
            
            
           // alert("this.list:::::" + (this.list)[0].TITLE);
    },
    writeContent() {
      this.$router.push({ 
        path: '/todo/create/'
      });
    }, showInfo(regdate) {
      //alert(regdate);
      this.$router.push({
        path: '/todo/create/'+regdate
      });
    }
  },
  computed: {
    rows() {
      return this.items.length;
    }
  }
};

