import axios from 'axios';


// 배포시 
const API_URL = '/';

// 테스트 springboot 연결
//const API_URL = 'http://localhost:8090/';


 
//const API_URL = '/api/auth/';
//var jobj = null;
   
class AuthService {
  login(user) {
    alert('로그인 테스트 입니다!!!');
    return axios
      .post(this.$SEND_API_URL + 'api/auth/signin.do', {
        username: user.username,
        age: user.age
      }, {
        headers: {'Content-Type': 'application/json'}
      })
      .then(response => {
        alert('응답값::::::'+ JSON.stringify(response.data.USER_AGE));
        //jobj = JSON.parse(response);

       // if (response.data.accessToken) {

         alert('user.age :' + user.age );
         alert('response.data.USER_AGE :' + response.data.USER_AGE );

        if (user.age == response.data.USER_AGE ) {

          alert('서버호출됨 !! ');
          localStorage.setItem('user', JSON.stringify(response.data));

        }else{
          alert('패스워드 확인!!');
        }

        return response.data;
      });
  }



  ulogin(user) {
    //alert('ulogin 로그인 테스트 입니다!!!');
    //alert(user.userid);
    //return axios.post(API_URL + '/api/auth/signin.do', {
     return axios.post(API_URL + 'api/auth/login.do', {
        LOGID: user.userid,
        PASS: user.pass
      }, {
        headers: {'Content-Type': 'application/json'}
      })
      .then(response => {
        //alert('응답값  PASS::::::'+ JSON.stringify(response.data.PASS));
        //alert('응답값  LOGIN_CHK ::::::'+ JSON.stringify(response.data.LOGIN_CHK));

        if ("T" == response.data.LOGIN_CHK ) {
          alert('서버호출됨 !! ');
          localStorage.setItem('user', JSON.stringify(response.data));

        }else{
          alert('패스워드 확인!!');
        }

        return response.data;
      });
  }



  logout() {
    //alert('1111');
    localStorage.removeItem('user');
  }

  register(user) {
    return axios.post(this.$SEND_API_URL + 'api/auth/signup', {
      username: user.username,
      email: user.email,
      age: user.age
    });
  }
}

export default new AuthService();